<template>
  <div class="content">
    <el-container style="height: 650px; border: 1px solid #eee;">
      <transition name="in" mode="out-in">
        <el-aside width="200px" class="ads" v-show="show">
          <el-menu :default-openeds="['1', '7']">
            <router-link to="/home">
              <el-menu-item index="1">
                <template slot="title"><i class="el-icon-menu"></i></template>
              </el-menu-item>
            </router-link>
            <el-submenu index="2">
              <template slot="title"><i class="el-icon-sold-out"></i></template>
              <el-menu-item-group>
                  <el-menu-item index="2-1" @click="showMenu">
                    <template slot="title"></template>
                  </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title"><i class="el-icon-phone-outline"></i></template>
              <el-menu-item-group>
                  <el-menu-item index="3-1" @click="showBusiness">
                    <template slot="title"></template>
                  </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title"><i class="el-icon-goods"></i></template>
              <el-menu-item-group>
                <router-link to="/input">
                  <el-menu-item index="4-1"></el-menu-item>
                </router-link>
                <router-link to="/output">
                  <el-menu-item index="4-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="5">
              <template slot="title"><i class="el-icon-printer"></i></template>
              <el-menu-item-group>
                <router-link to="/addOrder">
                  <el-menu-item index="5-1"></el-menu-item>
                </router-link>
              </el-menu-item-group>
              <el-menu-item-group>
                <router-link to="/order">
                  <el-menu-item index="5-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="6">
              <template slot="title"><i class="el-icon-info"></i></template>
              <el-menu-item-group>
                <router-link to="/staff">
                  <el-menu-item index="6-1"></el-menu-item>
                </router-link>
                <router-link to="/register">
                  <el-menu-item index="6-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
      </transition>

      <el-container>
        <el-header style="width:100%;text-align: right;font-size: 16px;">
          <i @click="click" class="el-icon-more more" style="float: left;margin-top: 20px;font-size: 20px;cursor: pointer"></i>
          <span>{{name}}</span>
          <span style="cursor: pointer;color: cornflowerblue" @click="logOut"></span>
        </el-header>

        <el-main>
            <h3 style="margin-bottom: 50px"></h3>
            <el-table
              :data="outputMachineList"
              border
              stripe
              style="width: 100%"
            >
              <el-table-column prop="date" label="">
                <template slot-scope="scope">
                  {{scope.row.date | formatDate}}
                </template>
              </el-table-column>
              <el-table-column
                prop="people"
                label=""
                width="180">
                <template slot-scope="scope">
                  {{scope.row.people}}
                </template>
              </el-table-column>
              <el-table-column
                prop="motorName"
                label=""
                width="180">
                <template slot-scope="scope">
                  {{scope.row.motorName}}
                </template>
              </el-table-column>
              <el-table-column
                prop="motorAddress"
                label=""
                >
                <template slot-scope="scope">
                  {{scope.row.motorAddress}}
                </template>
              </el-table-column>
               <el-table-column
                prop="motorNumber"
                label=""
              >
                <template slot-scope="scope">
                  {{scope.row.motorNumber}}
                </template>
              </el-table-column>
              <el-table-column
                prop="motorMoney"
                label=""
              >
                <template slot-scope="scope">
                  {{scope.row.motorMoney}}
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                    {{scope.row.time}}
                  </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                    {{scope.row.remarks}}
                  </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                   <el-button type="primary" size="small" @click="isChange(scope.row)"></el-button>
                  </template>
                </el-table-column>
            </el-table>
            <el-button style="float: right;margin-right: 5%;margin-top: 10px"
                       type="primary" icon="el-icon-edit" @click="open"
            ></el-button>
          <el-dialog title="" :visible.sync="addDialog" @close="resetForm('addForm')">
            <el-form :model="addForm" :rules="rules2" ref="addForm" label-width="80px">
              <el-form-item label="" prop="date">
                <el-date-picker style="width: 100%;"
                                v-model="addForm.date"
                                type="datetime"
                                placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" prop="people">
                <el-input v-model="addForm.people" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="motorName">
                <el-input v-model="addForm.motorName" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="motorAddress">
                <el-input v-model="addForm.motorAddress" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="motorNumber" >
                <el-input  v-model="addForm.motorNumber" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="motorMoney">
                <el-input v-model="addForm.motorMoney" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="time">
                <el-input v-model="addForm.time" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="remarks">
                <el-input v-model="addForm.remarks" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('addForm')"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
           <el-dialog title="" :visible.sync="editDialog" @close="resetForm('editForm')">
              <el-form :model="editForm" :rules="rules3" ref="editForm" label-width="80px">
                 <el-form-item label="" prop="date">
                <el-date-picker style="width: 100%;"
                                v-model="editForm.date"
                                type="datetime"
                                placeholder=""  :disabled="true">
                </el-date-picker>
              </el-form-item>
                <el-form-item label="" prop="people">
                  <el-input type="text" v-model="editForm.people" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="motorName">
                  <el-input type="text" v-model="editForm.motorName" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="motorAddress">
                  <el-input type="text" v-model="editForm.motorAddress" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="motorNumber">
                  <el-input type="text" v-model="editForm.motorNumber" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="motorMoney">
                  <el-input type="text" v-model="editForm.motorMoney" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="time">
                  <el-input type="text" v-model="editForm.time"></el-input>
                </el-form-item>
                <el-form-item label="" prop="remarks">
                  <el-input type="text" v-model="editForm.remarks" ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="updateOut"></el-button>
                </el-form-item>
              </el-form>
            </el-dialog>
        </el-main>
        <el-row type="flex" justify="end" style="clear: both">
          <el-pagination background layout="total,prev, pager, next" :total="total" :page-size="5" @current-change="pageChange" ></el-pagination>
        </el-row>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  import axios from "axios"
  export default {
    mounted() {
      this.getData();
      //  cookie
      if(document.cookie !== ""){
        axios.post('/users/cookie').then(res=>{
          // console.log(res.data.data);
          this.name=res.data.data;
        }).catch(err=>{
          console.log(err)
        })
      }
    },
    name: "Output",
    data() {
      return{
        show:true,
        name:"",
        outputMachineList:[],
        input6: '',
        input7: '',
        input8: '',
        input9: '',
        input10: '',
        total:0,
        addForm: {
          date:'',
          people:'',
          motorName:'',
          motorAddress:'',
          motorMoney:'',
          motorNumber:'',
          time:'',
          remarks:''
        },
        addDialog:false,
        rules2: {
          date:[
            {type:'date',required:true,message:'',trigger:'blur'}
          ],
          people: [
            { required: true, message: '', trigger: 'blur' },
          ],
          motorName: [
            { message: '', trigger: 'blur' },
          ],
          motorAddress: [
            { message: '', trigger: 'blur' },
          ],
          motorMoney: [
            { required: true, message: '', trigger: 'blur' },
          ],
          motorNumber: [
            { required: true, message: '', trigger: 'blur' },
          ],
          time: [
            { message: '', trigger: 'blur' },
          ],
          remarks: [
            {message: '', trigger: 'blur' },
          ],
        },
        ruleForm2: {
          time:"",
          remarks:'',
        },
        //
        editForm:{
          "date":'',
          "people":'',
          "motorName":'',
          "motorAddress":'',
          "motorNumber":'',
          "motorMoney":'',
          "time":'',
          "remarks":''
        },
        //
        editDialog:false,
        rules3: {
          time: [
            {message: 'g', trigger: 'blur' },
          ],
          remarks: [
            {message: '', trigger: 'blur' },
          ],
        }
      }
    },
    methods:{
      showMenu:function(){
        // console.log(this.name)
        if(this.name === ""  || this.name === "" || this.name === ""
          || this.name === "" || this.name === ""){
          this.$router.push({
            path:"/product"
          })
        }else{
          alert('')
        }
      },
      showBusiness:function(){
        // console.log(this.name)
        if(this.name === ""  || this.name === "" || this.name === ""
          || this.name === "" || this.name === ""){
          this.$router.push({
            path:"/business"
          })
        }else{
          alert('')
        }
      },
      open:function(){
        this.addDialog = true;
      },
      resetForm:function(formName){
        if(formName === 'addForm'){
          this.addDialog = false;
        }
        this.$refs[formName].resetFields();
      },
      submitForm:function(formName){
        this.$refs[formName].validate((valid)=>{
          if(valid){
            //
            axios.post('/users/addOutputMachine',this.addForm)
              .then(response=>{
                var res = response.data;
                if(res.status === '0'){
                  //
                  this.$message.success('');
                  this.resetForm('addForm');
                  //
                  this.getData();
                }else{
                  //
                  this.$message.error(res.message);
                }
              }).catch(err=>{
              console.log(err);
            })
          }else{
            return false
          }
        })
      },
       resetForm:function(formName){
        if(formName === 'editForm'){
          //
          this.editDialog = false;
        }
        //
        this.$refs[formName].resetFields();
      },
      isChange:function(row){
        //
        this.editDialog = true;
        //row
        this.editForm.date = row.date;
        this.editForm.people = row.people;
        this.editForm.motorName = row.motorName;
        this.editForm.motorAddress = row.motorAddress;
        this.editForm.motorNumber = row.motorNumber;
        this.editForm.motorMoney = row.motorMoney;
        this.editForm.time = row.time;
        this.editForm.remarks = row.remarks;
      },
      updateOut:function(){
        axios.post('/users/updateOut',this.editForm)
          .then(response=>{
            var res = response.data;
            if(res.status === '0'){
              this.resetForm('editForm');
              this.$message.success('');
              this.getData();
            }
          }).catch(err=>{
          console.log(err);
        })
      },
      getData(page) {
        axios.get('/users/outputMachineList',{
          params:{
            page:page || 1,
            pageSize:5
          }
        }).then(response => {
          const res = response.data;
          this.outputMachineList = res.outputMachineList;
          this.total = res.count;
          // console.log(res.count);
        })
      },
      pageChange:function(value){
        this.getData(value);
      },
      click:function () {
        this.show=!this.show;
      },
      logOut: function(){
        axios.post("/users/logout").then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$router.push({
              path:"/login"
            })
            this.$message.success('');
          }
        })
      },
    },
  }
</script>

<style scoped>
  .el-container /deep/ .el-dialog {
    width: 80%;
    margin: 0 auto;
  }
  .in-enter{
    transform: translateX(-30%);
  }
  .in-enter-active{
    transition: all 0.5s;
    position: absolute;
  }
  a{
    text-decoration: none;
  }
  .fa-home,.fa-user-o{
    padding-left: 5px ;
    padding-right: 10px;
    font-size: 19px;
  }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    height: 650px;
    color: #333;
  }
</style>



