<template>
  <div class="content">
    <el-container style="height: 650px; border: 1px solid #eee;">
      <transition name="in" mode="out-in">
        <el-aside width="200px" class="ads" v-show="show">
          <el-menu :default-openeds="['1', '7']">
            <router-link to="/home">
              <el-menu-item index="1">
               <template slot="title"><i class="el-icon-menu"></i></template>
              </el-menu-item>
            </router-link>
            <el-submenu index="2">
               <template slot="title"><i class="el-icon-sold-out"></i></template>
              <el-menu-item-group>
                  <el-menu-item index="2-1" @click="showMenu">
                    <template slot="title"></template>
                  </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title"><i class="el-icon-phone-outline"></i></template>
              <el-menu-item-group>
                  <el-menu-item index="3-1" @click="showBusiness">
                    <template slot="title"></template>
                  </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title"><i class="el-icon-goods"></i></template>
              <el-menu-item-group>
                <router-link to="/input">
                  <el-menu-item index="4-1"></el-menu-item>
                </router-link>
                <router-link to="/output">
                  <el-menu-item index="4-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="5">
              <template slot="title"><i class="el-icon-printer"></i></template>
              <el-menu-item-group>
                <router-link to="/addOrder">
                  <el-menu-item index="5-1"></el-menu-item>
                </router-link>
              </el-menu-item-group>
              <el-menu-item-group>
                <router-link to="/order">
                  <el-menu-item index="5-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="6">
             <template slot="title"><i class="el-icon-info"></i></template>
              <el-menu-item-group>
                <router-link to="/staff">
                  <el-menu-item index="6-1"></el-menu-item>
                </router-link>
                <router-link to="/register">
                  <el-menu-item index="6-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
      </transition>

      <el-container>
        <el-header style="width: 100%;text-align: right;font-size: 16px;">
          <i @click="click" class="el-icon-more more" style="float: left;margin-top: 20px;font-size: 20px;cursor: pointer"></i>
          <span>{{username}}</span>
          <span style="cursor: pointer;color: cornflowerblue" @click="logOut"></span>
        </el-header>
        <div class="content-form">
          <div class="font"></div>
          <div class="form">
            <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="80px" class="demo-ruleForm">
              <el-form-item label="" prop="name">
                <el-input v-model="ruleForm2.name" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="number">
                <el-input v-model="ruleForm2.number" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="address">
                <el-input v-model="ruleForm2.address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="position">
                <el-input v-model="ruleForm2.position" placeholder=""></el-input>
               </el-form-item>
              <el-form-item label="" prop="date">
                <el-date-picker
                                v-model="ruleForm2.date"
                                type="datetime"
                                placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" prop="time">
                <el-input v-model="ruleForm2.time" placeholder=""></el-input>
               </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
                <el-button @click="resetForm('ruleForm2')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  import {isvalidPhone} from '../validate'
  import axios from "axios"
  export default {
    name: "Home",
    data() {
      const validPhone=(rule, value,callback)=>{
        if (!value){
          callback(new Error(''))
        }else  if (!isvalidPhone(value)){
          callback(new Error('11'))
        }else {
          callback()
        }
      };
      return{
        username:'',
        show:true,
        ruleForm2: {
          name:'',
          position:'',
          number:'',
          address:'',
          date:'',
          time:''
        },
        rules2: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 4, message: ' 2  4 ', trigger: 'blur' }
          ],
          position: [
            { required: true, message: '', trigger: 'blur' },
          ],
          number: [
            { required: true,message: '', trigger: 'blur', validator: validPhone }//
          ],
          address: [
            { required: true, message: '', trigger: 'blur' },
          ],
          date:[
            {type:'date',required:true,message:'',trigger:'blur'}
          ],
          time:[
            {message:'',trigger:'blur'}
          ],
        }
      }
    },
    mounted(){
      if(document.cookie !== ""){
        axios.post('/users/cookie').then(res=>{
          // console.log(res.data.data);
          this.username=res.data.data;
        }).catch(err=>{
          console.log(err)
        })
      }
    },
    methods:{
      showMenu:function(){
        // console.log(this.name)
        if(this.username === ""  || this.username === "" || this.username === ""
          || this.username === "" || this.username === ""){
          this.$router.push({
            path:"/product"
          })
        }else{
          alert('')
        }
      },
      showBusiness:function(){
        // console.log(this.name)
        if(this.username === ""  || this.username === "" || this.username === ""
        || this.username === "" || this.username === ""){
          this.$router.push({
            path:"/business"
          })
        }else{
          alert('')
        }
      },
      click:function () {
        this.show=!this.show;
      },
      submitForm:function(formName){
        this.$refs[formName].validate((valid)=>{
          if(valid){
            //
            axios.post('/users/register',this.ruleForm2)
              .then(response=>{
                var res = response.data;
                if(res.status === '0'){
                  //
                  this.$message.success('');
                  this.backLogin();
                  this.$refs[formName].resetFields();
                }else{
                  //
                  this.$message.error(res.message);
                }
              }).catch(err=>{
              console.log(err);
            })
          }else{
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      backLogin:function () {
        this.$router.push({
          path:"/staff"
        })
      },
      logOut: function(){
        axios.post("/users/logout").then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$router.push({
              path:"/login"
            })
            this.$message.success('');
          }
        })
      },
    }
  }
</script>

<style scoped>
  .el-input{
    width: 200px;
  }
  .in-enter{
    transform: translateX(-50%);
  }
  .in-enter-active{
    transition: all 0.5s;
    position: absolute;
  }
  a{
    text-decoration: none;
  }
  .fa-home,.fa-user-o{
    padding-left: 5px ;
    padding-right: 10px;
    font-size: 19px;
  }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  .content-form .font{
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin-top: 20px;
  }
  .content-form .form{
    width: 300px;
    margin: 30px auto;
  }

</style>



