import Vue from 'vue'
import Router from 'vue-router'
//
import Home from "../pages/Home"
//
import Login from "../pages/Login"
//
import Product from "../pages/Product"
//
import Business from "../pages/Business"
//
import Input from "../pages/Input"
//
import Output from "../pages/Output"
//
import AddOrder from "../pages/AddOrder"
//
import Order from "../pages/Order"
//
import Staff from "../pages/Staff"
//
import Register from "../pages/Register"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/",
      redirect:'/login'  //
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/product',
      name: 'Product',
      component: Product
    },
    {
      path: '/business',
      name: 'Business',
      component: Business
    },
    {
      path: '/input',
      name: 'Input',
      component: Input
    },
    {
      path: '/output',
      name: 'Output',
      component: Output
    },
    {
      path: '/order',
      name: 'Order',
      component: Order
    },
    {
      path: '/addOrder',
      name: 'AddOrder',
      component: AddOrder
    },
    {
      path: '/staff',
      name: 'Staff',
      component: Staff
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
  ]
})





