<template>
  <div class="content">
    <el-container style="height: 650px; border: 1px solid #eee;">
      <transition name="in" mode="out-in">
        <el-aside width="200px" class="ads" v-show="show">
          <el-menu :default-openeds="['1', '7']">
            <router-link to="/home">
              <el-menu-item index="1">
                <template slot="title"><i class="el-icon-menu"></i></template>
              </el-menu-item>
            </router-link>
            <el-submenu index="2">
              <template slot="title"><i class="el-icon-sold-out"></i></template>
              <el-menu-item-group>
                  <el-menu-item index="2-1" @click="showMenu">
                    <template slot="title"></template>
                  </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title"><i class="el-icon-phone-outline"></i></template>
              <el-menu-item-group>
                  <el-menu-item index="3-1" @click="showBusiness">
                    <template slot="title"></template>
                  </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title"><i class="el-icon-goods"></i></template>
              <el-menu-item-group>
                <router-link to="/input">
                  <el-menu-item index="4-1"></el-menu-item>
                </router-link>
                <router-link to="/output">
                  <el-menu-item index="4-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="5">
              <template slot="title"><i class="el-icon-printer"></i></template>
              <el-menu-item-group>
                <router-link to="/addOrder">
                  <el-menu-item index="5-1"></el-menu-item>
                </router-link>
              </el-menu-item-group>
              <el-menu-item-group>
                <router-link to="/order">
                  <el-menu-item index="5-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="6">
              <template slot="title"><i class="el-icon-info"></i></template>
              <el-menu-item-group>
                <router-link to="/staff">
                  <el-menu-item index="6-1"></el-menu-item>
                </router-link>
                <router-link to="/register">
                  <el-menu-item index="6-2"></el-menu-item>
                </router-link>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
      </transition>

      <el-container>
        <el-header style="width:100%;text-align: right;font-size: 16px;">
          <i @click="click" class="el-icon-more more" style="float: left;margin-top: 20px;font-size: 20px;cursor: pointer"></i>
          <span>{{name}}</span>
          <span style="cursor: pointer;color: cornflowerblue" @click="logOut"></span>
        </el-header>

        <el-main>
          <div class="font"></div>
          <div class="form">
            <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="80px" class="demo-ruleForm">
              <el-form-item label="" prop="orderId">
                <el-input v-model="ruleForm2.orderId" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="date">
                <el-date-picker style="width: 200px;"
                                v-model="ruleForm2.date"
                                type="datetime"
                                placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" prop="companyName">
                <el-input v-model="ruleForm2.companyName" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="motorId">
                <el-input v-model="ruleForm2.motorId" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="reason">
                <el-input v-model="ruleForm2.reason" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="number">
                <el-input  v-model="ruleForm2.number" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="status">
                <el-select v-model="ruleForm2.status" placeholder="" style="width: 200px;">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="SendDate">
                <el-input  v-model="ruleForm2.SendDate" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
                <el-button @click="resetForm('ruleForm2')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  import axios from "axios"
  export default {
    name: "AddOrder",
    data() {
      return{
        show:true,
        name:"",
        options: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        },
          {
            value: '',
            label: ''
          }],
        value: '',
        ruleForm2: {
          orderId:'',
          date:'',
          companyName:'',
          motorId:'',
          reason:'',
          number:'',
          status:'',
          SendDate:''
        },
        rules2: {
          orderId: [
            { required: true, message: '', trigger: 'blur' },
          ],
          date:[
            {type:'date',required:true,message:'',trigger:'blur'}
          ],
          companyName: [
            { required: true, message: '', trigger: 'blur' },
          ],
          motorId: [
            { required: true, message: '', trigger: 'blur' },
          ],
          reason: [
            { required: true, message: '', trigger: 'blur' },
          ],
          number: [
            { required: true, message: '', trigger: 'blur' },
          ],
          status: [
            { required: true, message: '', trigger: 'blur' },
          ],
          SendDate:[
            {message:'',trigger:'blur'}
          ],
        }
      }
    },
    methods:{
      showMenu:function(){
        // console.log(this.name)
        if(this.name === ""  || this.name === "" || this.name === ""
          || this.name === "" || this.name === ""){
          this.$router.push({
            path:"/product"
          })
        }else{
          alert('')
        }
      },
      showBusiness:function(){
        // console.log(this.name)
        if(this.name === ""  || this.name === "" || this.name === ""
        || this.name === "" || this.name === ""){
          this.$router.push({
            path:"/business"
          })
        }else{
          alert('')
        }
      },
      click:function () {
        this.show=!this.show;
      },
      submitForm:function(formName){
        this.$refs[formName].validate((valid)=>{
          if(valid){
            //
            axios.post('/users/addOrder',this.ruleForm2)
              .then(response=>{
                var res = response.data;
                if(res.status === '0'){
                  //
                  this.$message.success('');
                  this.backLogin();
                  this.$refs[formName].resetFields();
                }else{
                  //
                  this.$message.error(res.message);
                }
              }).catch(err=>{
              console.log(err);
            })
          }else{
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      backLogin:function () {
        this.$router.push({
          path:"/order"
        })
      },
      logOut: function(){
        axios.post("/users/logout").then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$router.push({
              path:"/login"
            })
            this.$message.success('');
          }
        })
      },
    },
    mounted() {
      //  cookie
      if(document.cookie !== ""){
        axios.post('/users/cookie').then(res=>{
          // console.log(res.data.data);
          this.name=res.data.data;
        }).catch(err=>{
          console.log(err)
        })
      }
    },

  }
</script>

<style scoped>
  .el-input{
    width: 200px;
  }
  .font{
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin-top: 20px;
  }
  .form{
    width: 300px;
    margin: 30px auto;
  }
  .in-enter{
    transform: translateX(-30%);
  }
  .in-enter-active{
    transition: all 0.5s;
    position: absolute;
  }
  a{
    text-decoration: none;
  }
  .fa-home,.fa-user-o{
    padding-left: 5px ;
    padding-right: 10px;
    font-size: 19px;
  }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    height: 650px;
    color: #333;
  }
</style>



